<template>
  <div>
    <!-- Header -->
    <app-header headerClass="header--white"></app-header>

    <!-- Cart -->
    <div class="checkout">
      <div class="container">
        <!-- Heading -->
        <div class="checkout__heading">
          <h1>Checkout</h1>
          <img src="img/icons/checkout-icons-2.svg" alt="checkout-icons" class="checkout__icons">
        </div>
        <!-- Body -->
        <div class="checkout__body">
          <!-- Shipping Information -->
          <div class="checkout__shipping">
            <h3>Shipping Information</h3>
            <form class="checkout__inputs">
              <div class="input-group">
                <input type="text" class="input input--bordered" placeholder="First Name" v-model="checkoutForm.firstName" ref="firstName" @input="removeInvalidClass">
                <span class="checkout__input-message">Not a valid first name</span>
              </div>
              <div class="input-group">
                <input type="text" class="input input--bordered" placeholder="Last Name" v-model="checkoutForm.lastName" ref="lastName" @input="removeInvalidClass">
                <span class="checkout__input-message">Not a valid last name</span>
              </div>
              <div class="input-group">
                <input type="text" class="input input--bordered" placeholder="Email" v-model="checkoutForm.email" ref="email" @input="removeInvalidClass">
                <span class="checkout__input-message">Not a valid email</span>
              </div>
              <div class="input-group">
                <input type="text" class="input input--bordered" placeholder="Phone Number" v-model="checkoutForm.phone" ref="phone" @input="removeInvalidClass">
                <span class="checkout__input-message">Not a valid phone number</span>
              </div>
              <div class="input-group">
                <input type="text" class="input input--bordered" placeholder="Address" v-model="checkoutForm.address" ref="address" @input="removeInvalidClass">
                <span class="checkout__input-message">Not a valid address</span>
              </div>
              <div class="input-group">
                <input type="text" class="input input--bordered" placeholder="City" v-model="checkoutForm.city" ref="city" @input="removeInvalidClass">
                <span class="checkout__input-message">Not a valid city</span>
              </div>
              <div class="input-group">
                <input type="text" class="input input--bordered" placeholder="Postal Code / ZIP" id="zipCode" v-model="checkoutForm.zipCode" ref="zipCode" @input="removeInvalidClass">
                <span class="checkout__input-message">Not a valid zip code</span>
              </div>
              <div class="input-group">
                <input type="text" class="input input--bordered" placeholder="Country" id="country" v-model="checkoutForm.country" ref="country" @input="removeInvalidClass">
                <span class="checkout__input-message">Not a valid country</span>
              </div>
            </form>
          </div>
          <!-- Your Cart -->
          <div class="checkout__cart">
            <h3>Your Cart</h3>
            <!-- Products -->
            <div class="checkout__products">
              <div class="checkout__product" v-for="obj in shoppingCart" :key="obj.product.id">
                <img :src="obj.product.images[0]" alt="product-img">
                <h4>{{ obj.product.name }}</h4>
                <div class="checkout__product-quantity">x{{ obj.quantity }}</div>
                <div class="checkout__product-price">${{ obj.product.price }}</div>
              </div>
            </div>
            <!-- Total Price -->
            <div class="checkout__total-price">
              <p>Total Cost:</p>
              <div class="price">${{ shoppingCartTotal }}</div>
            </div>
          </div>
        </div>
        <!-- Footer -->
        <div class="checkout__footer">
          <router-link to="/cart" tag="a" class="checkout__continue">
            <img src="img/icons/arrow-black.svg" alt="black-arrow"> Return to Cart
          </router-link>
          <a class="btn btn--orange ml-auto" @click="submitCheckoutForm">Order Products</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../components/Header.vue';
import { required, email } from 'vuelidate/lib/validators';

export default {
  data() {
    return {
      checkoutForm: {
        firstName: '',
        lastName: '',
        email: '',
        phone: '',
        address: '',
        city: '',
        zipCode: '',
        country: ''
      }
    }
  },
  components: {
    'app-header': Header
  },
  computed: {
    shoppingCart() {
      return this.$store.getters.shoppingCart;
    },
    shoppingCartTotal() {
      return this.$store.getters.shoppingCartTotal;
    }
  },
  methods: {
    removeInvalidClass(e) {
      e.target.closest('.input-group').classList.remove('invalid');
    },
    submitCheckoutForm() {
      let errors = 0;

      if (!this.checkoutForm.firstName) {
        this.$refs.firstName.closest('.input-group').classList.add('invalid');
        errors++;
      }

      if (!this.checkoutForm.lastName) {
        this.$refs.lastName.closest('.input-group').classList.add('invalid');
        errors++;
      }

      if (this.checkoutForm.email && this.validateEmail(this.checkoutForm.email)) {
      } else {
        this.$refs.email.closest('.input-group').classList.add('invalid');
        errors++;
      }

      if (!this.checkoutForm.phone) {
        this.$refs.phone.closest('.input-group').classList.add('invalid');
        errors++;
      }

      if (!this.checkoutForm.address) {
        this.$refs.address.closest('.input-group').classList.add('invalid');
        errors++;
      }

      if (!this.checkoutForm.city) {
        this.$refs.city.closest('.input-group').classList.add('invalid');
        errors++;
      }

      if (!this.checkoutForm.zipCode) {
        this.$refs.zipCode.closest('.input-group').classList.add('invalid');
        errors++;
      }

      if (!this.checkoutForm.country) {
        this.$refs.country.closest('.input-group').classList.add('invalid');
        errors++;
      }

      if (!errors > 0 && this.shoppingCart.length > 0) {
        this.$store.dispatch('removeAll', 'remove');
        this.$router.push('/thank-you');
      }
    },
    validateEmail(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(email).toLowerCase());
    }
  }
}
</script>